import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { QueryCard }  from '../card/Card.stories';
import { SearchInput } from '../search/Search.stories';

/**
 * UI component for wiki search
 */
export const WikiSearch = ({ placeholder, ...props }) => {
  const [query, setQuery] = useState("");
  const [searchResponse, setSearchResponse] = useState([]);

  const makeApiCalls = (event) => {
    console.log(event)
    if (event.key === 'Enter') {
      fetch(`https://en.wikipedia.org/w/rest.php/v1/search/title?q=${query}&limit=10`)
        .then(response => response.json())
        .then(data => {
            setSearchResponse(data.pages)
            console.log(data)
        });
    }
  }

  return (
    <div>
        {Array.isArray(searchResponse) ? (
                <>
                    {data.Users.map((user) => (
                        <UserCard key={user.id} {...user} />
                    ))}
                </>
            ) : null}
      <QueryCard />
      <SearchInput  
        placeholder={placeholder}
        onChange={setQuery}
        onKeyUp={makeApiCalls}
      />
    </div>  
  );
};

WikiSearch.propTypes = {
  /**
   * Is this the principal call to action on the page?
   */
  placeholder: PropTypes.string,
  /**
   * Optional click handler
   */
  onClick: PropTypes.func,
};

WikiSearch.defaultProps = {
  onClick: undefined,
};
